import React from "react"

const bgColors = {
  infrastructure: "rgba(91, 25, 221, 0.3)",
  PM: "rgba(53, 177, 129, 0.3)",
  design: "rgba(248, 188, 89, 0.3)",
  "back-end": "rgba(1, 96, 250, 0.5)",
  "front-end": "rgba(209, 151, 255, 0.5)",
}

function TechBox({ techData, selected }) {
  return (
    <div className="tech-box-wrapper">
      <div
        className="image-wrapper"
        style={{ background: bgColors[techData.type] }}
      >
        {selected !== '' && [ selected !== techData.type && <div className="overlay" />]} 
        <img src={techData.img} alt={techData.name} />
      </div>
      <p className="name">{techData.name}</p>
    </div>
  )
}

export default TechBox
